import { ethers } from "ethers";
import { formatFixed, parseFixed } from '@ethersproject/bignumber'

const uniV2RouterAbi = new ethers.utils.Interface([
  'function getAmountsIn(uint, address[] memory) view returns (uint[] memory)',
  'function getAmountsOut(uint, address[] memory) view returns (uint[] memory)',
]);

const buildinChainUrl = {
  "eth": "https://eth-mainnet.gateway.pokt.network/v1/5f3453978e354ab992c4da79",
  "bsc": "https://bsc-dataseed.binance.org/",
  "polygon": "https://polygon-rpc.com/",
  "ftm": "https://rpc.ftm.tools/",
  "avax": "https://api.avax.network/ext/bc/C/rpc",
}

const providerFromUrl = (url) => {
  return new ethers.providers.JsonRpcProvider(url);
}

const providerFromChain = (chainName) => {
  return providerFromUrl(buildinChainUrl[chainName]);
}

const initContract = (address, abi, provider) => {
  return new ethers.Contract(address, abi, provider);
}

const E18 = ethers.BigNumber.from('1000000000000000000'); // 1e18

const utils = {
  formatFixed,
  parseFixed,
  providerFromUrl,
  providerFromChain,
  initContract,
}

const constants = {
  uniV2RouterAbi,
  E18,
}

const prefix = "async (ethers, utils, constants, fetch, history, location, localStorage, navigator, window) => {"
const subfix = "}"

export const compileCode = (code) => {
  let finalCode = prefix + code + subfix;
  try {
    return eval(finalCode);
  } catch (e) {
    console.log(e);
    return undefined;
  }
}

export const runCode = async (code) => {
  let f = compileCode(code);
  try {
    return await f(ethers, utils, constants);
  } catch (e) {
    console.log(e);
    return undefined;
  }
}
